<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>我的对接
                <img v-if="one" class="search" src="../../../assets/personal/fold.png" alt="" @click="showCha1">
                <img v-if="two" class="search" src="../../../assets/personal/fold.png" alt="" @click="showCha2">
            </p>
        </section>
        <section class="middle">
                <md-tab-bar
                        v-model="current"
                        :items="items"
                        :has-ink="false"
                        @change="onTabChange"
                ></md-tab-bar>
                <div class="none" v-if="!listLeft.length"  :style="{height: Height}">暂无数据</div>
                <md-scroll-view v-if='listLeft.length' :style="{height: Height}" @end-reached="$_onEndReached" :auto-reflow="true" ref="scrollView" :scrolling-x="false" @scroll="$_onScroll">
                    <div
                            v-for="i in listLeft"
                            class="scroll-view-item"
                            :key="i.id"
                            @click="$_onItemClick(i)">
                        <img :src="i.img" alt="">
                        <div class="article">
                            <p v-html="i.name"></p>
                            <span>{{i.unitName}}<md-button v-if="i.dockingStatus == 1" type="sure" size="small" inline @click="checkTime(i.dockingStartTime, i.dockingEndTime, i.dockingId)">查看对接时间</md-button></span>
                        </div>
                    </div>
                    <md-scroll-view-more slot="more" :is-finished="isFinished">
                    </md-scroll-view-more>
                </md-scroll-view>
                <md-picker
                        ref="typePicker"
                        v-model="isshowCha1"
                        :data="cha1"
                        @confirm="onChaConfirm1"
                        title="设备类型"
                ></md-picker>
            <md-picker
                    ref="typePicker"
                    v-model="isshowCha1"
                    :data="cha1"
                    @confirm="onChaConfirm1"
                    title="设备类型"
            ></md-picker>
            <md-picker
                    ref="typePicker"
                    v-model="isshowCha1"
                    :data="cha1"
                    @confirm="onChaConfirm1"
                    title="设备类型"
            ></md-picker>
            <md-picker
                    ref="typePicker"
                    v-model="isshowCha1"
                    :data="cha1"
                    @confirm="onChaConfirm1"
                    title="设备类型"
            ></md-picker>
                <md-picker
                        ref="typePicker"
                        v-model="isshowCha2"
                        :data="cha2"
                        @confirm="onChaConfirm2"
                        title="单位类型"
                ></md-picker>
                <md-dialog
                        :closable="true"
                        v-model="iconDialog.open"
                        :btns="iconDialog.btns"
                >
                    <img src="../../../assets/personal/docking.png" alt="">
                    <p>为您提供的对接时间段是：</p>
                    <p>{{ iconDialog.startTime }} - {{ iconDialog.endTime }}</p>
                    <div style="margin-top: 20px">
                        <md-button type="default" size="small" inline round @click="noSure">取消对接</md-button>
                        <md-button type="sure" size="small" inline round @click="sure">确定对接</md-button>
                    </div>
                </md-dialog>
        </section>
    </main>
</template>

<script>
import { getList } from '@/apiUrl/personal/docking.js'
import { sendDate } from '@/apiUrl/personal/server.js'
import { formatTime } from '../../../utils/public'
import { TabBar, Swiper, Icon, Picker, Button, Dialog, SwiperItem, ScrollView, ScrollViewMore, Toast } from 'mand-mobile'
export default {
  name: 'News',
  components: {
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [Button.name]: Button,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [Icon.name]: Icon,
    [Picker.name]: Picker,
    [Dialog.name]: Dialog,
    [ScrollViewMore.name]: ScrollViewMore,
    [Toast.name]: Toast
  },
  data () {
    return {
      // 当前选中tab
      current: 1,
      // tab项
      items: [{ name: 1, label: '成果' }, { name: 2, label: '需求' }, { name: 3, label: '设备' }, { name: 4, label: '专家' }, { name: 5, label: '单位' }],
      listLeft: [],
      isFinished: false,
      one: false,
      two: false,
      iconDialog: {
        open: false
      },
      isshowCha1: false,
      isshowCha2: false,
      Height: '900px',
      listRight: [],
      searchParams: {
        type: 1,
          status: 0,
        pageSize: 10,
        pageNumber: 1
      },
      cha1: [
        [
          { text: '正在对接', value: 1 },
          { text: '对接完成', value: 2 }
        ]
      ],
      cha2: [
        [
          { text: '企业', value: 2 },
          { text: '高校院所', value: 3 },
          { text: '服务机构', value: 4 },
          { text: '其他单位', value: 5 }
        ]
      ]
    }
  },
  mounted () {
    this.one = true
    this.getList()
    this.Height = window.innerHeight - 84 + 'px'
  },
  methods: {
    showCha1 () {
      this.isshowCha1 = true
    },
    showCha2 () {
      this.isshowCha2 = true
    },
    checkTime (start, end, id) {
      this.iconDialog.open = true
      this.iconDialog.startTime = formatTime(start, 'Y-M-D')
      this.iconDialog.endTime = formatTime(end, 'Y-M-D')
      this.iconDialog.id = id
    },
    getList () {
      getList(this.searchParams).then(res => {
        this.listLeft = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    onSwiperChange (from, to) {
      this.current = to
    },
    noSure () {
      const args = {
        status: 2,
        id: this.iconDialog.id
      }
      sendDate(args).then(() => {
        this.iconDialog.open = false
      })
    },
    sure () {
      const args = {
        status: 3,
        id: this.iconDialog.id,
        dockingStartTime: this.iconDialog.startTime,
        dockingEndTime: this.iconDialog.endTime
      }
      sendDate(args).then(res => {
        Toast.succeed('对接成功')
        this.iconDialog.open = false
      }).catch(err => {
        Toast.failed(err)
      })
    },
    onTabChange (item, index) {
      item.name === 1 || item.name === 2 || item.name === 3 || item.name === 4 ? this.one = true : this.one = false
      item.name === 5 ? this.two = true : this.two = false
      this.searchParams.pageNumber = 1
      this.searchParams.type = item.name
      this.getList()
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      this.searchParams.pageNumber++
      getList(this.searchParams).then(res => {
        this.listLeft = this.listLeft.concat(res.data)
        if (res.data.length === 0) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }).catch(err => {
        Toast.failed(err)
      })
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    },
    // 回退
    goBack () {
      this.$router.push('/personal')
    },
    // 类型选择确认
    onChaConfirm1 (columnsValue) {
      console.log(`[Mand Mobile] DatePicker Confirm\nvalue: ${JSON.stringify(columnsValue)}`)
      this.searchParams.status = columnsValue[0].value
      this.getList()
    },
    // 类型选择确认
    onChaConfirm2 (columnsValue) {
      console.log(`[Mand Mobile] DatePicker Confirm\nvalue: ${JSON.stringify(columnsValue)}`)
      this.searchParams.status = columnsValue[0].value
      this.getList()
    }
  }
}
</script>

<style lang="stylus" scoped>
    .middle
        position fixed
        top 88px
        width 100%
    /deep/.md-tab-bar-list .is-active
        color #021955!important
        background #FFF!important
        border-bottom 4px solid #021955
    /deep/.md-tab-bar-item
        background #fff!important
        color #999999!important
    .md-example-child-tab-bar-6 .md-swiper
        background #FFF
        .md-swiper-item
            padding 70px
            box-sizing border-box
            font-size 24px
            line-height 1.5
            color #666
    .md-example-child-scroll-view-0
        height 600px
        background #FFF
        .scroll-view-item
            margin-bottom 30px
            text-align center
            font-size 28px
            font-family DINAlternate-Bold
            height 174px
    .scroll-view-container
        .scroll-view-item
            margin-bottom  50px
            padding-bottom 20px
            border-bottom 2px solid rgba(153,153,153,.5)
            display flex
            justify-content space-between
            img
                width 260px
                height 152px
            .article
                width 410px
                text-align left
                margin 10px 0
                display flex
                flex-direction column
                justify-content space-between
                p
                    color #333333
                    font-size 28px
                    font-weight Medium
                    text-overflow ellipsis
                    overflow hidden
                    display -webkit-box
                    -webkit-line-clamp 2
                    -webkit-box-orient vertical
                span
                    display flex
                    align-items center
                    line-height 24px
                    justify-content: space-between
                    font-size 24px
                    img
                        width 24px
                        margin-right 10px
        .md-scroll-view-more
            justify-content center
    /deep/.md-tab-bar .md-tab-bar-list
        background-color #ffffff!important
        border-top 0!important
    /deep/.default
        text-align center
        float left
        height:50px;
        margin-right 100px
        border-radius:8px;
    /deep/.sure
        float left
        text-align center
        height 50px;
        background rgba(2,25,85,1)
        border-radius 8px
        color white
    /deep/.sure:after
        border none
    .md-scroll-view
        padding 20px
</style>
